
import { mapGetters, mapActions } from 'vuex';
import formItem from '@/component/form-item.vue';
import ycRodioGroup from '@/component/yc-radio-group.vue';
import XDialog from 'vux/src/components/x-dialog/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
components: {
formItem,
ycRodioGroup,
XDialog,
PopupPicker
},
name: 'saveEduEper',
data() {
return {
submitForm: false,
transformsShow: false,
formData: {
schoolName: '',
diploma: [],
major: '',
timeQuantum: '2011-2015'
},
rlues: {
schoolName: {
focus: false,
blur: true
},
major: {
focus: false,
blur: true
},
timeQuantum: {
focus: false,
blur: true
}
},
list1: [['本科', '大专', '中专', '职业技术学院', '小学', '无']],
timeQuantumList: [],
timeQuantumValue: [],
showPopupPicker1: false,
showPopupPicker2: false,
isChecked: 0
};
},
methods: {
...mapActions([]),
saveEduEper() {
this.submitForm = true;
this.$router.push({
path: '/annual-change-info'
});
},
typeSelect() {
this.showPopupPicker1 = true;
},
getFocus(info) {
this.rlues[info].focus = true;
this.rlues[info].blur = false;
},
getBlur(info) {
this.rlues[info].focus = false;
this.rlues[info].blur = true;
},
inputEmpty(info) {
this.formData[info] = '';
},
submitFormBtn() {
this.submitForm = true;
},
getTimeData(start, end) {
let arr = [];
for (let i = start; i <= end; i++) {
arr.push(i);
}
this.timeQuantumList = [arr, arr];
},
getTime() {
this.showPopupPicker2 = true;
},
getTimeConfirm() {
this.formData.timeQuantum =
this.timeQuantumValue[0] + '-' + this.timeQuantumValue[1];
}
},
watch: {},
computed: {
...mapGetters([])
},
created() {
this.getTimeData(2000, 2300);
},
mounted() {
let elem = document.querySelector('#vux_view_box_body');
setTimeout(() => {
elem.style.paddingBottom = '0';
}, 10);
}
};
